import TabBar from "./TabBar";
import { listData } from "../../server/employee";

import MescrollVue from "mescroll.js/mescroll.vue";
import "mescroll.js/mescroll.min.css";
export default {
  name: "employee",
  components: { TabBar, MescrollVue },
  data() {
    return {
      class1: "class1",
      class2: "class2",
      mescroll: null, // mescroll实例对象
      mescrollDown: {
        callback: this.downCallBack,
        clearEmptyId: "main",
        isBoth: false,
        isBounce: true,
        // inOffsetRate :1
      },
      mescrollUp: {
        // 上拉加载的配置.
        callback: this.getList,
        htmlNodata: '<p class="upwarp-nodata">到底啦~~~</p>',
        noMoreSize: 5,
        page: {
          num: 0,
          size: 50,
          total: 0
        },
        empty: {
          warpId: "main",
          icon: "./static/mescroll/mescroll-empty.png",
          tip: "暂无相关数据~" //提示
        },
        htmlLoading:
          '<p class="upwarp-progress mescroll-rotate"></p><p class="upwarp-tip">加载中..</p>'
      },
      dataList: [],
      imgFlag: false,
      flag: false,
      value: "",
      imgUrl: require("@/assets/img/Group.png"),
      removeUrl: require("@/assets/img/remove.png"),
      pages: {
        page: 1,
        size: 50
      }
    };
  },
  created() {},
  mounted() {
    history.pushState(null, null, document.URL);
    window.addEventListener('popstate', function () {
      history.pushState(null, null, document.URL);
    })
  },

  methods: {
    search() {
      this.handleSeach();
    },
    mescrollInit(mescroll) {
      this.mescroll = mescroll;
    },
    downCallBack(mescroll) {
      setTimeout(function() {
        mescroll.endSuccess();
      }, 1500);
    },
    handleSeach() {
      let searchList = [];
      const that = this;
      listData({
        page: this.pages.page,
        limit: this.pages.size,
        searchStr: this.value
      }).then(e => {
        if (e.data.code == 200) {
          this.searchList = e.data.data.list;
          this.dataList = this.searchList;
        }
      });
    },
    getList(page, mescroll) {
      const that = this;
      listData({
        page: page.num,
        limit: page.size,
        searchStr: this.value
      }).then(e => {
        if (e.data.code == 200) {
          var arr = e.data.data.list;
          let data = page.num == 1 ? [] : this.dataList;
          this.dataList = this.dataList.concat(arr);
          //  console.log(this.dataList);
          this.$nextTick(() => {
            this.mescroll.endSuccess(arr.length);
          });
        }
      });
    },
    handleImg() {
      this.flag = !this.flag;
      this.imgFlag = !this.imgFlag;
    },
    handlePushEnter() {
      localStorage.removeItem("employeeName");
      localStorage.removeItem("customerEmployeeNo");
      this.$router.push({ path: "/addEmployee" });
    },
    handleLive() {
      this.$router.push({ path: "/leaveEmployee/id" });
    }
  }
};



